.menu_container{
    padding: 0 150px;
    width: 100%;
    height: 150px;
}

.menu_container .menu_top{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 57%;
    /*background-color: red;*/
}

.menu_container .menu_bottom{
    height: 43%;
    /*background-color: yellow;*/
}

.menu_bottom .lst_menu .catalog_dropdown{
    position: absolute;
    /*background-color: red;*/
    background-color: white;
    width: auto;
    height: 375px;
    z-index: 999;
    display: flex;
    transform: translate(-10%);
    margin-top: 15px;
}

.catalog_dropdown_content{
    position: relative;
    height: 100%;
}

.catalog_dropdown_content .u_flexbox{
    width: 100%;
    height: 100%;
    display: flex;
}

.catalog_dropdown_categories{
    width: 200px;
    border-right: 1px solid black;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 20px;
}

.catalog_dropdown_categories a:hover{
    background-color: black;
}

.catalog_dropdown_items{
    /*background-color: yellow;*/
    width: 100%;
    display: grid;
    grid-template-columns: auto auto auto auto;
    /*grid-template-rows: auto;*/
    overflow: auto;
    padding: 20px;
    gap: 10px 0;
}

.catalog_dropdown_items ul{
    display: flex;
    flex-direction: column;
    gap: 5px;
    list-style-type: none;
}

.catalog_dropdown_items .col{
    text-align: left;
    margin: 0 15px;
}

.catalog_dropdown_items .col .title{
    font-weight: 800;
    text-transform: uppercase;
}

.catalog_dropdown_items .col a:hover{
    text-decoration: underline;
}

.logo{
    width: 170px;
    height: 42px;
}

i{
    font-size: 40px;
}

.search_container{
    display: flex;
    align-items: center;
}

.search{
    margin-left: 50px;
    padding: 10px;
    width: 450px;
    height: 40px;
    border-radius: 50px;
}

.lst_menu{
    height: 100%;
}

.menu_container .categories{
    width: 100%;
    height: 100%;
    list-style-type: none;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 35px;

}

.menu_container li{
    text-decoration: none;
}

.menu_container li a{
    text-decoration: none;
    color: rgb(13, 49, 69);
    font-size: 12px;
    font-weight: 700;
}


/****************************************  RESPONSIVE   **************************************************/

@media screen and (max-width: 1050px) {
    .catalog_dropdown_items{
        grid-template-columns: auto auto auto;
        gap: 10px 0;
    }
}