.news_product_container{
    padding: 20px 100px;
    width: 100%;
    height: auto;
}

.news_product_container h2{
    font-size: 25px;
    font-weight: 700;
}

.news_product_container .mySwiper{
    margin-top: 50px;
}

.news_product_container .swiperSlide{
    background-color: white;
    height: auto;
    padding: 20px 0;
}

.swiperSlide img{
    margin-bottom: 10px;
}

.product_details{
    /*position: absolute;*/
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    height: 275px;
    /*bottom: 0;*/
    padding: 0 20px;
    gap: 10px;
}

#newProduct2 .product_details{
    height: 200px;
}

.swiperSlide p{
    color: black;
    font-size: 14px;
}

.swiperSlide .attribut{
    color: grey;
}

.swiperSlide .price{
    font-size: 18px;
    color: rgb(1, 50, 76);
    font-weight: 600;
}

.swiperSlide .btn{
    margin-top: 15px;
    height: 41px;
}

.swiperSlide .btn_ajout{
    background-color: yellow;
    border-radius: 50px;
    border: none;
    font-size: 14px;
    padding: 11px;
    text-decoration: none;
    color: black;
}