.appli_container{
    position: relative;
    width: 100%;
    height: 400px;
    display: flex;
    justify-content: center;
  }
  
  .appli_desc{
    padding: 30px 0;
    width: 60%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }
  
  .appli_desc h1{
    font-size: 25px;
    color: white;
  }
  
  .img_appli{
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: -100;
  }
  
  .img{
    background-position: 50% 50%;
    background-size: cover;
    background-repeat: no-repeat;
    background-image: url("https://www.panini.fr/media/wysiwyg/PEC-1188-MyPanini-parallax_desktop_v3.jpg");
    position: absolute;
    top: 0px;
    left: 0px;
    width: 1349px;
    height: 500px;
    pointer-events: none;
    margin-top: -17.7969px;
    transform: translate3d(0px, -18.2578px, 0px);
  }
  
  .appli_desc .btn{
    margin-top: 15px;
    height: 41px;
  }
  
  .appli_desc .btn_ajout{
    background-color: yellow;
    border-radius: 50px;
    border: none;
    font-size: 14px;
    padding: 11px;
    text-decoration: none;
    color: #01324c;
  }
  
  