.header{
    position: relative;
    padding: 10px 150px 0px 150px;
    width: 100%;
    height: 75px;
    background-color: #00324B;
}

p{
    margin: 0;
    color: white;
}

.list_help{
    width: 100%;
    height: 50%;
}

.header ul{
    width: 100%;
    height: 100%;
    list-style-type: none;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    gap: 35px;
    color: #abb8bf;
    font-size: 11px;
}

.header li{
    text-decoration: none;
}

li:hover{
    text-decoration: underline;
    cursor: pointer;
}