.footer_container{
    width: 100%;
    height: 520px;
    background-color: #01324c;
    color: white;
    padding: 35px 135px;
}

.footer_container .footer_top{
    display: flex;
    gap: 80px;
}

.footer_top .social_container{
    display: flex;
    flex-direction: column;
    gap: 75px;
    font-size: 14px;
}

.social_container .social_network{
    display: flex;
    gap: 10px;
}

.social_container .social_network i{

    font-size: 14px;
}

.footer_top .logo{
    width: 287px;
    height: 72px;
}

.footer_container .row{
    display: flex;
    justify-content: center;
    gap: 20px;
}

.footer_container .col{
    display: flex;
    flex-direction: column;
    width: 155px;
    text-align: start;
    gap: 15px;
}

.footer_container .col h6{
    font-size: 13px;
    font-weight: 700;
}

.footer_container .col span{
    font-size: 13px;
}

.footer_container .diviser{
    border-bottom: 1px solid white;
    margin: 30px 0;
}

.footer_container .footer_bottom{
    display: flex;
    flex-direction: column;
    gap: 20px;
    font-size: 13px;
    font-weight: 400;
}
.footer_bottom .infos_social{
    color: #c5ced3;
    display: flex;
    flex-direction: column;
    gap: 20px;
    line-height: 20px;
}

.footer_bottom .political{
    display: flex;
    justify-content: center;
    gap: 20px;
    font-weight: 600;
}

.footer_bottom .political a{
    color: yellow;
    text-decoration: none;
}

.footer_bottom .political a:hover{
    color: white;
    text-decoration: underline;
}

.footer_bottom .cookies{
    margin: 15px 0;
    font-size: 13px;
    color: yellow;
}