.img_container{
    height: 260px;
}

.bandeau{
  display: flex;
  height: 15px;
  background-color: #00324B;
  margin: 0
}

.indicator {
    width: 15px;
    height: 5px;
    color: blue;
    text-align: center;
    cursor: pointer;
    border: 1px grey solid;
    background: grey;
  }
  .indicator.active {
    background: black;

  }

  .react-slideshow-container .default-nav{
    margin: 20px;
    background: rgba(255, 255, 255, 0) !important 
  }

  .indicators{
    display: flex;
    gap: 10px;
    align-items: center;
    height: 15px;
    background-color: #00324B;
    margin: 0 !important;
  }
  