.pres_container{
    padding: 50px 100px;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(225px, 255px));
    grid-gap: 30px;
    /*grid-auto-rows: minmax(140px, auto);*/
    height: auto;
    justify-content: center;
}

.img_pres_container{
    height: 125px;
}

.img_pres_container img{
    width: 100%;
    height: 125px;
}